import uCharts from '@/components/u-charts/u-charts.js';
var _self;
var canvaArea = null;
var canvaColumn = null;

export default {
	data() {
		return {
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			serverData: '',
		}
	},
	onLoad() {
		_self = this;
		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData() {
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data: {},
				success: function(res) {
					console.log(res.data.data)
					_self.serverData = res.data.data;
					let Area = {
						categories: [],
						series: []
					};
					Area.categories = res.data.data.Area.categories;
					Area.series = res.data.data.Area.series;
					_self.showArea("canvasArea", Area);
					let ColumnStack = {
						categories: [],
						series: []
					};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					ColumnStack.categories = res.data.data.ColumnStack.categories;
					ColumnStack.series = res.data.data.ColumnStack.series;
					_self.showColumnStack("canvasColumnStack", ColumnStack);
				},
				fail: () => {
					_self.tips = "网络错误，小程序端请检查合法域名";
				},
			});
		},
		showArea(canvasId, chartData) {
			canvaArea = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'area',
				fontSize: 11,
				legend: true,
				dataLabel: true,
				dataPointShape: true,
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				colors: ['#FF5C5C', '#FFBB00', '#979DAB'],
				xAxis: {
					disableGrid: true,
				},
				yAxis: {
					gridColor: '#F5F7F7',
					data: {

					}
				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					area: {
						type: 'straight',
						opacity: 0.2,
						addLine: true,
						width: 2,
						gradient: true,
					}
				}
			});

		},
		touchArea(e) {
			canvaArea.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		},



		showColumnStack(canvasId, chartData) {
			canvaColumn = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'column',
				legend: {
					show: true
				},
				colors: ['#FF5C5C', '#FFBB00', '#979DAB'],
				fontSize: 11,
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				animation: true,
				categories: chartData.categories,
				series: chartData.series,
				xAxis: {
					disableGrid: true,
				},
				yAxis: {
					disabled: true,
					gridColor: '#F5F7F7',
				},
				dataLabel: true,
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					column: {
						type: 'stack',
						width: _self.cWidth * _self.pixelRatio * 0.1 / chartData.categories.length
					}
				}
			})
		},
		touchColumn(e) {
			canvaColumn.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		}






	}
}
